import { BASE_API, URLS } from '@/constants/urls'
import { ISearch } from '@/typing/interfaces/ISearch'
import axios from 'axios'

export const axiosInstance = axios.create({
  baseURL: BASE_API
})

export const Api = {
  async find(payload: ISearch) {
    return await axiosInstance.get(URLS.find, {
      params: payload
    })
  }
}
